<template>
  <div class="container p-1 p-b-2">
    <!-- Loader -->
    <div
      class="loader bus-progress-loader flexbox align-items-center justify-center zindex-10"
      v-show="show_Loader"
    >
      <div class="text-primary">
        <i class="fas fa-spinner fa-pulse fa-5x"></i>
      </div>
    </div>
    <!--Loader-->

    <!-- No Buses Available-->
    <div
      v-if="town_schedules.length < 1"
      class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center mobile-justify-start p-t-xs p-b-xs"
    >
      <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5">
        <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
        <h3 class="title font-h-1 mobile-font-sm">Sorry, no buses available</h3>
      </span>
    </div>
    <!-- No Buses Available-->

    <div
      v-if="town_schedules.length > 0"
      class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center mobile-justify-start p-t-xs p-b-xs"
    >
      <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5">
        <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
        <h3
          class="title font-h-1 mobile-font-sm"
        >Leaving from {{ trip.town_from.town_name }} to {{ trip.town_to.town_name }}</h3>
      </span>
    </div>

    <div
      v-if="town_schedules.length > 0"
      class="heading mr-t-1 p-t-1 p-b-1 mobile-flex-wrap flexbox align-items-center pos-relative"
    >
      <h3
        class="trip-date mr-0 font-bold color-secondary font-h-1 mobile-font-sm"
      >{{ trip.travelling_on | formatDate}}</h3>
      <div
        class="instructions font-h-1 pos-absolute mr-0 color-primary mobile-pos-relative"
      >Select Bus by choosing the price</div>
    </div>

    <!-- Where we load the results -->
    <div class="bus-list-container" v-if="town_schedules.length > 0">
      <!-- Render the buses here -->

      <!-- Single Bus List Item Container Starts Here -->
      <div
        class="bus-list-card-container pos-relative p-t-1 p-b-1"
        v-for="(town_schedule, index) in town_schedules"
        :key="town_schedule.schedule_id"
      >
        <div
          class="bus-card-wrapper flexbox pos-relative mobile-flex-wrap"
          :class="{ 'is_active_bus': isSelectedBus(town_schedule.schedule_id) }"
        >
          <div class="bus-info">
            <div class="header p-1 flexbox justify-between color-secondary font-bold font-xs">
              <div class="bus-number">{{ index + 1 }}</div>
              <div class="bus-type">{{ town_schedule.bus_type_name }} Bus</div>
              <div class="bus-rating">
                <div class="ratings-bar">
                  <span>
                    <i class="fa fa-star"></i>
                  </span>
                  <span>
                    <i class="fa fa-star"></i>
                  </span>
                  <span>
                    <i class="fa fa-star"></i>
                  </span>
                  <span>
                    <i class="fa fa-star"></i>
                  </span>
                  <span>
                    <i class="fa fa-star-half"></i>
                  </span>
                </div>
              </div>
            </div>
            <div class="content p-1">
              <div
                class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
              >
                <span>{{ trip.town_from.town_name }}</span>
                <span>{{ trip.town_to.town_name }}</span>
              </div>
              <div
                class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
              >
                <span>Departure Time</span>
                <span>{{ town_schedule.latest_departure_time }}</span>
              </div>
              <div
                class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
              >
                <span>Avaialable Seats</span>
                <span>{{ town_schedule.available_vip + town_schedule.available_first_class + town_schedule.available_business }}</span>
              </div>
            </div>
          </div>
          <div class="bus-price-wrapper flex-1 flexbox">
            <a
              href="#"
              class="price-label flex-1 flexbox flex-dir-col justify-center align-items-center p-1"
              @click.prevent="$emit('busSelected', town_schedule, false)"
              v-for="(price, index) in town_schedule.prices"
              :key="index"
            >
              <div>
                <span class="mr-b-sm">{{ price.seat_type_name }}</span>
                <span class="mr-b-sm">
                  <em class="currency">KES</em>
                  {{ price.price_kes_amount | formatCurrency }}
                </span>
              </div>
            </a>
          </div>
        </div>
        <!-- Notice label here -->
        <div v-if="town_schedule.is_highway == 1" class="notice-label">This bus might delay</div>
      </div>
      <!-- /Single Bus List Item Container Ends Here -->
    </div>
    <!-- /bus-list-wrapper -->
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters } from "vuex";

export default {
  name: "AppBusResults",
  data() {
    return {
      currentDate: new Date()
    };
  },
  computed: mapGetters(["town_schedules", "trip", "show_Loader"]),
  methods: {
    isSelectedBus(schedule_id) {
      return this.trip.selected_bus.schedule_id == schedule_id;
    }
  },
  mounted() {
    //scroll to top
    window.scrollTo(0, 0);
  }
};
</script>


