<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Include AppLoader (Show Progress On Page) -->
      <AppLoader v-if="show_Loader" />

      <!-- Include AppTripFilter Componenet -->
      <AppTripFilter v-if="showAppTripFilter" />

      <!-- Include AppBusResults Componenet  -->
      <AppBusResults v-if="showAppBusResults" @busSelected="busSelectedEvent" />

    </section>

    <!-- App Footer -->
    <AppFooter />

  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppTripFilter component
import AppTripFilter from "@/components/AppTripFilter.vue";

//AppTLoader component
import AppLoader from "@/components/AppLoader.vue";

//AppBusResults Componenet
import AppBusResults from "@/components/AppBusResults.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

export default {
  name: "Buses",
  components: {
    AppHeader,
    AppTripFilter,
    AppLoader,
    AppBusResults,
    AppFooter
  },
  data() {
    return {
      showAppTripFilter: true,
      showAppLoader: false,
      showAppBusResults: true,
      seats: null
    };
  },
  computed: mapGetters([
    "show_Loader",
    "trip",
    "selected_seats",
    "return_selected_seats",
    "passenger_details",
    "bus_seats",
    "total_amount",
    "trip_info",
    "seats_locked",
    "showModal"
  ]),
  methods: {
    ...mapActions([
      "vehicleSelected",
      "showLoader",
      "hideLoader",
      "getTripInfo",
      "getLockSeats",
      "hideModal",
      "showTheModal",
      "getReturningDetails"
    ]),
    busSelectedEvent: function(bus, is_return) {
      //Update Schedule Id, Boarding & Picking points
      this.vehicleSelected({ bus: bus, is_return: is_return });

      //scroll to top
      window.scrollTo(0, 0);

      if (!is_return) {
        //Redirect to Bus seats page
        this.$router.push("BusSeats");
      } else {
        //Redirect to Return Bus seats page
        this.$router.push("ReturnBusSeats");
      }
    },
  },
  mounted() {
    let self = this;

    //scroll to top
    window.scrollTo(0, 0);

    //ProgressBar Demo
    self.showAppLoader = true;
    setTimeout(() => {
      self.showAppLoader = false;
    }, 2000);
  }
};
</script>
